:root {
  /* ––––––––– SPACINGS ––––––––– */
  --spacing-0: 0rem; /* 0px */
  --spacing-1: 0.0625rem; /* 1px */
  --spacing-2: 0.125rem; /* 2px */
  --spacing-4: 0.25rem; /* 4px */
  --spacing-6: 0.375rem; /* 6px */
  --spacing-8: 0.5rem; /* 8px */
  --spacing-10: 0.625rem; /* 10px */
  --spacing-12: 0.75rem; /* 12px */
  --spacing-16: 1rem; /* 16px */
  --spacing-20: 1.25rem; /* 20px */
  --spacing-24: 1.5rem; /* 24px */
  --spacing-32: 2rem; /* 32px */
  --spacing-40: 2.5rem; /* 40px */
  --spacing-48: 3rem; /* 48px */
  --spacing-56: 3.5rem; /* 56px */
  --spacing-64: 4rem; /* 64px */
  --spacing-80: 5rem; /* 80px */
  --spacing-96: 6rem; /* 96px */
  --spacing-128: 8rem; /* 128px */
  --spacing-160: 10rem; /* 160px */
  --spacing-192: 12rem; /* 192px */
  --spacing-256: 16rem; /* 256px */
  --spacing-320: 20rem; /* 320px */
  --spacing-384: 24rem; /* 384px */
  --spacing-512: 32rem; /* 512px */
  --spacing-640: 40rem; /* 640px */
  --spacing-768: 48rem; /* 768px */
  --spacing-1024: 64rem; /* 1024px */
  --spacing-1280: 80rem; /* 1280px */
  --spacing-1440: 90rem; /* 1440px */

  /* ––––––––– BORDER WIDTHS ––––––––– */
  --border-width-none: 0;
  --border-width-xs: 0.0625rem; /* 1px */
  --border-width-sm: 0.125rem; /* 2px */
  --border-width-md: 0.1875rem; /* 3px */
  --border-width-lg: 0.25rem; /* 4px */

  /* ––––––––– BORDER RADIUS ––––––––– */
  --border-radius-sm: 0.125rem; /* 2px */
  --border-radius-md: 0.25rem; /* 4px */
  --border-radius-lg: 0.5rem; /* 8px */
  --border-radius-xl: 0.75rem; /* 12px */
  --border-radius-2xl: 1rem; /* 16px */
  --border-radius-3xl: 1.5rem; /* 24px */
  --border-radius-4xl: 2rem; /* 32px */
  --border-radius-full: 50%;
}
